import { Component } from '@angular/core';
import { StewardClientService, MlkDataTableColumn, MlkMoreActions } from 'steward-client'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  columns: Array<MlkDataTableColumn> = [
    {columnName: "Role Name", fieldName: "name"}, 
    {columnName: "Description", fieldName: "description"},
    {columnName: "Date Created", isDateColumn: true, fieldName: "creationDate"}
  ];
   moreActions: MlkMoreActions;

   constructor(){
    this.moreActions = new MlkMoreActions([
      {actionName: "View"}, {actionName: "Delete"}
    ], "roleId", "More Actions");

   }
}
